.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #eee;
}

.container {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 80%;
  transform: translate(-50%, -50%);
}

.house {
  cursor: pointer;
  font-size: 30px;
  font-family: 'Oak', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 700;

  .bg-fill {
    opacity: 0;
    transition: opacity 0.3s ease-in;
  }

  text {
    fill: #333;
    transition: fill 0.3s ease-in;
  }

  &:hover, &.active {
    .bg-fill {
      opacity: 1;
    }
    text {
      fill: #333;
    }
  }
}